
import { defineComponent, ref, toRefs, reactive, Ref } from "vue";
import { useStore } from "vuex";
import Draggable from "vuedraggable";
import FabricCard from "@/components/FabricCard.vue";
import CollectionAddFabricsDialog from "./CollectionAddFabricsDialog.vue";

import {
  ChangeOrderParams,
  CollectionCreateInputs,
  CollectionDetailResponse,
  CollectionFabrics
} from "@/typings/collection";
import { PostCreateCollection, PostUpdateCollection } from "@/typings/store";
import {
  seasonOptions,
  showLabelOptions,
  statusOptions,
  yearOptions
} from "@/views/Collection/options";
import { deleteCollection, putCollectionsChangeOrder } from "@/apis/collection";
import { ElNotification } from "element-plus";
import { useRouter } from "vue-router";
import { OptionItem } from "@/typings/asideFilter";
import { getMemberTeams } from "@/apis/member";
import { MemberTeam } from "@/typings/member";
import { iCreateDialog } from "@/typings/component";
import { FabricResult } from "@/typings/fabric";

const initForm = () => ({
  id: 0,
  status: "private",
  public_for: null,
  show_label: false,
  name: "",
  owner: "",
  description: "",
  year: null,
  season: [],
  theme: "",
  hash_tag: ""
});

const createParams = (form: CollectionCreateInputs) => {
  const params: any = {
    name: form.name,
    description: form.description,
    season: form.season,
    show_label: form.show_label,
    theme: form.theme,
    hash_tag: form.hash_tag,
    status: form.status,
    public_for: form.public_for,
    // TODO remove creator when api fixed.
    owner: 4,
    creator: 4,
    sport_type: []
  };
  if (form.year) params.year = form.year;
  return params;
};

const _statusOptions = [
  ...statusOptions,
  {
    label: "Public for Company",
    value: "public_for_company",
    checked: false
  }
];

const Component = defineComponent({
  name: "CreateDialog",
  components: { FabricCard, Draggable, CollectionAddFabricsDialog },
  props: {
    editable: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const router = useRouter();
    const store = useStore();
    const { editable } = toRefs(props);

    const collectionAddFabricsDialog: Ref<iCreateDialog | null> = ref(null);
    const visible = ref(false);
    const companyOptions = ref<OptionItem[]>([]);
    const collectionFabrics = ref<CollectionFabrics[]>([]);
    const publicForName = ref("");

    const form = reactive<CollectionCreateInputs>(initForm());

    const init = async () => {
      if (editable.value) {
        companyOptions.value = [
          {
            label: publicForName.value,
            value: form.public_for,
            checked: false
          }
        ];
        return;
      }
      const { data } = await getMemberTeams();
      companyOptions.value = data.map((it: MemberTeam) => ({
        label: it.name || "",
        value: it.id || 0,
        checked: false
      }));
    };

    const create = async () => {
      const params = createParams(form);
      await store.dispatch(PostCreateCollection, params);
      visible.value = false;
      emit("load");
    };

    const edit = async () => {
      const params = createParams(form);
      await store.dispatch(PostUpdateCollection, {
        id: form.id,
        params
      });
      const orderParams: ChangeOrderParams = {
        id: `${form.id}`,
        orders: collectionFabrics.value.map(
          (it: CollectionFabrics) => it.fabric.id
        )
      };
      await putCollectionsChangeOrder(orderParams);
      visible.value = false;
      emit("load");
    };

    const open = async (data?: CollectionDetailResponse) => {
      visible.value = true;
      if (editable.value && data) {
        form.id = data.id;
        form.status = data.status;
        form.show_label = data.show_label;
        form.name = data.name;
        form.description = data.description;
        form.year = data.year;
        form.season = data.season;
        form.theme = data.theme;
        form.hash_tag = data.hash_tag;
        form.public_for = data.public_for;
        publicForName.value = data.public_for_name || "";
        collectionFabrics.value = data.collection_fabrics;
      }
      await init();
    };

    const onConfirm = async () => {
      if (editable.value) {
        await edit();
      } else {
        await create();
      }
    };

    const onInit = () => {
      const emptyForm = initForm();
      Object.keys(emptyForm).forEach(
        // @ts-ignore
        (key: string) => (form[key] = emptyForm[key])
      );
    };

    const onDeleteConfirm = async () => {
      if (!form.id) return;
      await deleteCollection(form.id);
      ElNotification({
        type: "success",
        title: "Success",
        message: `Delete collection ${form.name} successfully`
      });
      await router.push({ name: "Collections" });
    };

    const onAddFabricClick = () => {
      if (!collectionAddFabricsDialog.value) return;
      collectionAddFabricsDialog.value.open({
        id: form.id,
        data: collectionFabrics.value
      });
    };

    const onRemoveFabric = (data: FabricResult) => {
      collectionFabrics.value = collectionFabrics.value.filter(
        (it: CollectionFabrics) => it.fabric.id !== data.id
      );
    };

    const onCollectionAddFabricsSubmit = (data: CollectionFabrics[]) => {
      collectionFabrics.value = data;
    };

    return {
      collectionAddFabricsDialog,
      form,
      visible,
      statusOptions: _statusOptions,
      showLabelOptions,
      yearOptions,
      seasonOptions,
      companyOptions,
      collectionFabrics,
      onCollectionAddFabricsSubmit,

      open,
      onConfirm,
      onInit,
      onRemoveFabric,
      onDeleteConfirm,
      onAddFabricClick
    };
  }
});

export default Component;
