
import { defineComponent, ref, computed, watch } from "vue";
import FabricCardSelectable from "@/components/FabricCardSelectable.vue";
import SearchBar from "@/components/SearchBar.vue";
import {
  FabricCardSelectableEmit,
  FabricResult,
  FabricsParams,
  FabricsResponse
} from "@/typings/fabric";
import { getFabrics } from "@/apis/fabric";
import { CollectionFabrics } from "@/typings/collection";

const Component = defineComponent({
  name: "CreateDialog",
  components: {
    FabricCardSelectable,
    SearchBar
  },
  setup(_, { emit }) {
    const visible = ref(false);
    const collectionId = ref(0);
    const selectedFabrics = ref<FabricResult[]>([]);
    const pageCount = ref(0);
    const searchValue = ref("");
    const page = ref(1);

    const fabricOptions = ref<FabricsResponse | null>(null);
    const selectedIds = computed(() =>
      selectedFabrics.value.map((it: FabricResult) => it.id)
    );

    const init = async () => {
      const params: FabricsParams = {
        q: searchValue.value,
        to_collection: collectionId.value,
        page: page.value
      };
      const { data } = await getFabrics(params);
      fabricOptions.value = data;
      pageCount.value = data.total_pages;
    };

    watch([searchValue], async () => {
      page.value = 1;
      await init();
    });

    const open = async ({
      id,
      data
    }: {
      id: number;
      data: CollectionFabrics[];
    }) => {
      visible.value = true;
      collectionId.value = id;
      selectedFabrics.value = data.map((it: CollectionFabrics) => it.fabric);
      await init();
    };

    const onFabricSelect = (data: FabricCardSelectableEmit) => {
      if (data.selected) {
        selectedFabrics.value = selectedFabrics.value.filter(
          (fabric: FabricResult) => fabric.id !== data.id
        );
      } else {
        selectedFabrics.value.push(data.data as FabricResult);
      }
    };

    const onSearchChange = (value: any) => {
      if (typeof value === "string") {
        searchValue.value = value;
      }
    };

    const onConfirm = async () => {
      if (!fabricOptions.value) return;

      const params: CollectionFabrics[] = selectedFabrics.value.map(
        (it: FabricResult): CollectionFabrics => ({
          fabric: it,
          price: 0,
          id: ""
        })
      );
      emit("submit", params);
      visible.value = false;
    };

    const onPaginationChange = async (_page: number) => {
      page.value = _page;
      await init();
    };

    return {
      visible,
      selectedFabrics,
      fabricOptions,
      page,
      pageCount,
      selectedIds,
      searchValue,

      open,
      onSearchChange,
      onConfirm,
      onFabricSelect,
      onPaginationChange
    };
  }
});

export default Component;
